@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap);
/* Reset */
*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}
body {
   font-family:'Montserrat', Helvetica,Arial, sans-serif;
  font-size: 16px;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f4f4f4;
}
li{
  list-style: none;
}
a{
  text-decoration: none;
}

.nav__bar{
  height: 3rem;
  width: 100vw;
  background-color: blue;
  position: -webkit-sticky;
  position: sticky;
  top:0;
  
}
.nav_items{
  max-width: 60rem;
  margin: 0 auto;
  height: 100%;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position:relative;
}
.nav-tab{
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}
.nav-tab:hover{
  opacity:0.85;
}
.active{
  border-bottom: 1px solid #f4f4f4;
}

.page__header{
  text-align: center;
  margin: 3rem 0; 
}
/* Form */
.form__container{
  height: 100%;
  width: 100vw;
}

.todo__container{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  padding: 2rem;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

.todo__card{
  box-shadow: 3px 3px 15px 1px rgba(114, 114, 129, 0.5);
  padding: 1.5rem;
  max-width: 35rem;
  background-color: aliceblue;
  position:relative;
}

.todo__actions{
  display: flex;
  justify-content: flex-end;
  position: absolute;
  right:1rem;
  bottom: 0.5rem;
}
.todo__btn{
  cursor:pointer;
  border:none;
  padding: 0.5rem 0.8rem;
  color:#fff;
  font-weight:600;
  font-size: 0.85rem;
  margin-left: 0.75rem;
  border-radius:8px;
  -webkit-border-radius:8px;
  -moz-border-radius:8px;
  -ms-border-radius:8px;
  -o-border-radius:8px;
}
.todo__info{
  width:100%;
  display:flex;
  align-items: center;
  padding-bottom: 2rem;
  border-bottom: 1px solid #666;
}
.todo_dates{
  margin: 1rem 0 2rem;
  font-weight: 600;
  font-size: 0.85rem;
  color: #666;
}
.todo_dates * + * {
  margin-top:0.5rem;
}
.checkbox{
  height: 1.3rem;
  width:1.3rem;
  cursor:pointer;
  margin-right: 0.8rem;
}
h2{
  width: 90%;
}
.todo__btn:hover{
  opacity: 0.8;
}
.edit_btn{
  background: green;
}
.delete_btn{
  background: red;
}
.restore_btn{
  background:seagreen;
}

.form_group{
  max-width:30rem;
  height: auto;
  margin: 0 auto;
  padding: 1rem;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  background-color: #c0c0c0;
}
label{
  display: block;
  margin: 0.5rem 0;
  font-weight: bold;
  color: #111;
}
.input_field{
  width: 100%;
  height: 2.5rem;
  padding: 0.8rem;
  font-size: 1.2rem;
}
.btn{
  cursor:pointer;
  width: 100%;
  text-align: center;
  font-weight: 600;
  color: #fff;
  background-color:blue;
  border:none;
  padding: 0.8rem;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  margin-top: 1.2rem;
}
.errMsg{
  color: red;
  font-weight: 600;
  font-size: 0.8rem;
}
